export const checkUserAgent = () => {
    const userAgent = navigator.userAgent.toLowerCase();
    if ( userAgent.indexOf('android') > -1) {
        //
        return "android";
    } else if ( userAgent.indexOf("iphone") > -1||userAgent.indexOf("ipad") > -1||userAgent.indexOf("ipod") > -1 ) {
        //IOS
        return "ios";
    } else {
        //,  
        return "other";
    }
}